<template>
  <van-row class="shop-block">
    <van-col @click="toShopDetail(item.collectId)" class="line" v-for="(item, index) in parentData.shopList" :key="index">
      <van-swipe-cell>
          <template slot="default">
            <div class="default-block">
              <div class="head-portrait">
                <img v-if="item.pic" :src="item.pic | imageToHttps" alt="商店封面图"/>
                <img v-else :src="getDefaultImageUrl(item)" alt="商店封面图"/>
              </div>
              <div class="detail">
                <div class="title">
                  {{item.name}}
                </div>
                <div class="fans">
                  {{item.collectionNum}}粉丝
                </div>
                <!-- <div class="tag"> -->
                  <!-- <div class="authentication">
                    官方认证
                  </div> -->
                  <!-- <div class="vip">
                    LV10盟主{{item.productTitle}}
                  </div> -->
                  <!-- <div class="fans">
                    {{item.collectionNum}}粉丝
                  </div>
                </div> -->
              </div>
            </div>
          </template>
          <template slot="right">
            <van-button square type="danger" text="删除" @click="cancelShop(item)"/>
          </template>
        </van-swipe-cell>
    </van-col>
    <!--店铺收藏列表为空-->
    <div class="no-data" v-show="parentData.shopList.length === 0 && isNoData">
      <img :src="`${$cdnImageUrl}/personalCenter/new_collect.png` | imageToHttps" alt=""/>
      <div>
        暂无收藏店铺/场馆
      </div>
    </div>
  </van-row>
</template>

<script>
/**
 * vant框架
 */
import Vue from 'vue';
import { SwipeCell } from 'vant';
const logo = require('@/assets/imgs/shop/logo.png')

Vue.use({ SwipeCell });

/*
 * 我的收藏列表
*/
export default {
  name: 'collectionList',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
    isNoData: {
      type: null,
      default: function () {
        return {}
      },
    }
  },
  data() {
    return {
    }
  },
  onLoad() {
  },
  methods: {
    getDefaultImageUrl(store) {
      if (this.$util.isSyBird() || Number(store.synStoreType) !== 9) {
        return `${this.$cdnImageUrl}/sybird_default_logo.png`
      } else {
        return logo
      }
    },
    /**
     * @description 删除已收藏的店铺
     */
    cancelShop(item) {
      console.log(item);
      const context = this;
      // 回调父组建的取消收藏方法
      context.$emit('cancelShopBack', item);
    },

    /**
     * @description  点击跳转到店铺
    */
    toShopDetail(storeId) {
      const context = this;
      context.$router.push({
        path: `/shopDetail?storeId=${storeId}`,
      });
    },
  },
}
</script>

